.Header {
  display: flex;
  flex-direction: row;
  justify-content: start;
  position: fixed;
  padding: 20px ;
}
.menu-list {
  ul {
    list-style-type: none;
    display: block;
    li {
      display: inline-block;
      padding-right: 20px;
      font-weight: bold;
    }
  }
  .sub-list{
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    padding: 20px;
    margin-top: 10px;
    line-height: 2;
    li{
      border-bottom: 1px solid #eeeeee;
      font-weight: normal;
    }
  }
}

.banner{
  background-image: url("../asset/img-banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 700px;
}